		<!-- 今日战绩 -->
		<div class="record">
			<div class="record-title">
				<div class="record-title-left">
					<span></span>
					今日战绩
				</div>
				<div class="record-title-right" @click="goMore">
					更多
					<van-icon name="arrow" />
				</div>
			</div>
			<div class="record-content">
				<div class="record-item">
					<div class="record-data">{{page_data.win_num}}</div>
					<div class="record-name">今日赚取</div>
				</div>
				<div class="record-item">
					<div class="record-data">{{page_data.rank}}</div>
					<div class="record-name">排行榜</div>
				</div>
				<div class="record-item">
					<div class="record-data">{{page_data.win_count}}</div>
					<div class="record-name">猜中</div>
				</div>
			</div>
		</div>

		<!-- 涨/跌 -->
		<van-popup v-model="showGuessPopup">
			<div class="guess-box">
				<img v-if="guess_type == '1'" src="../assets/imgs/up.png" alt="">
				<img v-if="guess_type == '2'" src="../assets/imgs/down.png" alt="">
				<van-icon name="cross" class="close-guess" @click="closeGuessFun"/>
				<div class="guess-content">
					<div class="guess-title">第{{page_data.btc_result.btc_id + 2}}期</div>
					<div class="guess-text">竞猜金额：</div>
					<div class="guess-input">
						<van-field class="guess-input-left" @input="moneyInputFun" v-model="money" type="number" placeholder="请输入竞猜金额" />
						<span class="guess-input-right">蚪金</span>
					</div>
					<div class="guess-text">您的蚪金金额：{{user_info.money}}</div>
					<div class="guess-item">
						<span :class="{'active':money_item_index == index}" v-for="(item,index) in money_item" :key="index" @click="moneyItemClick(item,index)">+{{item}}</span>
					</div>
					<div class="guess-btn-box">
						<img src="../assets/imgs/up-down-btn.png" alt="" @click="confirmGuess">
					</div>
					
				</div>
			</div>
		</van-popup>

		<!-- 验证支付密码 -->
		<div class="popup" v-if="showPayPasswordPopup">
			<!-- 阴影 -->
			<div class="popup-shadow"></div>
			
			<div class="popup-password-input">
				<div class="popup-password-input-title">
					<van-icon name="cross" @click="closePayPassword"/>
					<span>输入支付密码，以验证身份</span>
				</div>
				<!-- 密码框 -->
				<van-password-input :value="payPw"/>
				<!-- 数字键盘 -->
				<van-number-keyboard :show="true" :hide-on-click-outside="false" @input="onInput" @delete="onDelete" />
			</div>
		</div>
	</div>
</template>

<script>

import { mapState } from 'vuex'
import { mapMutations } from 'vuex'

export default {
	name: 'home',
	data(){
		return{
			showGuessPopup:false,//下注框
			showPayPasswordPopup:false,//密码输入框
			payPw:'',//密码

			timeInterval: '',//当前期数结束时间倒计时
			timeResultInterval: '',//购买期数开奖时间啊in倒计时

			//页面信息
			page_data:{
				btc_result:{
					add_time:"",
					btc_id:"",
					btc_price:"",
					change:"",
					dispaly_name:"",
					
				},
				user_postbtc:{
					add_time:'',
					btc_id:'',
					dw_money:'',
					id:'',
					status:'',
					type:''
				},
				fall:0,
				rank:0,
				risa:0,
				win_count:0,
				win_num:0,
				next_time: ''
			},
			//当前期数结束时间备份
			next_time_tmp: 0,
			//购买期数开奖时间备份
			game_time_tmp:0,
			
			//竞猜场次
			guess_data:{
				name:"第0000001期",
				up:0,
				up_money:'',
				down:1,
				down_money:'500',
				end_time:600,
				money_all:'2301',
				result:'up'
			},
			//猜中次数最多
			most_data:{
				name:'小明',
				img:require('../assets/imgs/user.png'),
				money:'2300',
				times:10
			},
			money_item:[20,100,200,400],
			money_item_index:-1,
			money:'',//下注金额
			moneyTemp: '',
			guess_type: '1',//1 猜涨 2 猜跌
			//图表配置
			optionline:{
				xAxis: {
					type: 'category',
					show:false
				},
				yAxis: {
					type: 'value',
					show:false,
					min:'dataMin',
					max:'dataMax',
				},
				series: [{
					data: [820, 932, 901, 934,0, 1290, 1330, 1320,880,890,750,260,450,760],
					type: 'line',
					smooth: true,
					animation:false
				}],
				color:["#fff"]
				
			},
		}
	},
	computed:{
		...mapState(['user_info']),//用户信息
		//剩余分钟
		end_minute(){
			let end_minute = parseInt(Math.abs(this.page_data.next_time)/60);
			if(end_minute < 10){
				end_minute = '0' + end_minute;
			}
			return end_minute
		},
		//剩余秒数
		end_second(){
			let end_second = Math.abs(this.page_data.next_time)%60;
			if(end_second < 10){
				end_second = '0' + end_second;
			}
			return end_second
		},
		//开奖时间
		draw_time(){
			let now_date = new Date().getTime();
			let draw_date = new Date(now_date + Math.abs(this.game_time_tmp) * 1000);
            let hour = draw_date.getHours() < 10 ? "0" + draw_date.getHours() : draw_date.getHours();
            let minute = draw_date.getMinutes() < 10 ? "0" + draw_date.getMinutes() : draw_date.getMinutes();
            return hour + ":" + minute;
            // return year + "-" + month + "-" + day + " " + hour + ":" + minute + ":" + seconds;
		},
		
		//看涨百分比
		up_percent(){
			if(this.page_data.risa == 0 && this.page_data.fall == 0){
				return 50
			}else{
				return this.page_data.risa
			}
		},
		//看跌百分比
		down_percent(){
			if(this.page_data.risa == 0 && this.page_data.fall == 0){
				return 50
			}else{
				return this.page_data.fall
			}
		}

	},
	created(){
		
		this.utils.info('user_info...',this.user_info);
        if(this.$route.params.id){
			this.utils.info('有id...已登录...');
			this.user_info.user_id = this.$route.params.id;
		}else{
			this.utils.info('没有id...未登录...');
			this.user_info.user_id = 1;
        }
        this.utils.info('user_info...',this.user_info);


		//获取用户信息
		this.getUserInfo('1');

	},
	mounted(){
		//获取btn信息
		this.getBtnFun();

	},
	methods:{
		//返回
		goBack(){
			this.utils.info('返回app...');
			this.$bridge.callhandler('goBack', {tag:"返回app"}, (data) => {})
		},
		//去登录
		goLogin(){
			this.utils.info('去登录...');
			this.$bridge.callhandler('goLogin', {tag:"登录"}, (data) => {})
		},
		//战绩更多
		goMore(){
			this.utils.info('战绩更多');
			this.$bridge.callhandler('goMore', {tag:"战绩更多"}, (data) => {})
		},
		//玩法规则
		goRulePage(){
			this.$router.push({
				name:'rule'
			})
		},
		//显示验证密码
		showPayPassword(){
			this.showPayPasswordPopup = true;
		},
		//关闭验证密码
		closePayPassword() {
			this.showPayPasswordPopup = false
			this.payPw = '';

		},
		// 数字键盘 事件
		onInput(key) {
			let that = this
			this.payPw = (this.payPw + key).slice(0, 6);
			if (this.payPw.length == 6) {
				
				let option = {
                    pay_password: this.payPw,
					token: this.user_info.token,
					money: this.moneyTemp,
					type_id:this.guess_type
                }
                this.utils.showLoading();
                this.closePayPassword();
                this.http.post(this.http._HOST + 'wave/buy',option).then(result =>{
                    this.utils.info('猜涨跌...',result); 
                    this.utils.dismissLoading(); 
                    if(result.success){
						
						this.utils.showToast('竞猜成功');

						//刷新数据
						this.getUserInfo('1');
                       
                    }else{
                        this.utils.showAlert(result.message);
                    }
                })
				
			}
		},
		onDelete() {
			this.payPw = this.payPw.slice(0, this.payPw.length - 1);
		},
		//猜涨/跌 type 1涨 2跌
		guessFun(type){
			
			if(this.user_info.pay_password == ''){
                this.utils.showAlert('请先设置支付密码');
                return false;
            }

			this.guess_type = type;
			this.showGuessPopup = true;
			
		},
		//关闭猜涨/跌
		closeGuessFun(){
			this.showGuessPopup = false;
			this.money = '';
			this.money_item_index = -1;
		},
		//输入竞猜金额
		moneyInputFun(){
			
			this.utils.info('money1...',this.money);
			if(this.money_item_index > -1){
				this.money_item_index = -1;
			}
			if(this.money != ''){
				this.money = parseInt(this.money) + '';

				if(parseInt(this.money) > 400){
					this.money = 400 + '';
				}
				if(parseInt(this.money) > parseInt(this.user_info.dw_money)){
					this.money = parseInt(this.user_info.dw_money) + '';
				}
			}
			
		},
		//选择竞猜金额
		moneyItemClick(item,index){
			if(this.money_item_index == index){
				return false;
			}
			if(parseInt(item) > parseInt(this.user_info.dw_money)){
				this.utils.showToast('您的蚪金金额不足');
				return false;
			}
			this.money_item_index = index;
			this.money = item;
		},
		//确认猜涨跌
		confirmGuess(){
			if(this.money == '' || this.money == '0'){
				this.utils.showToast('请输入竞猜金额');
				return false;
			}

			this.moneyTemp = this.money;
			this.closeGuessFun();
			this.showPayPassword();
		},
		//初始化图表
		drawLine(){
	
			let chartline = this.$echarts.init(this.$refs.homeChartContent);
			chartline.setOption(this.optionline);

		},
		//获取btn信息
		getBtnFun(){
			this.$jsonp('https://data.block.cc/api/v1/price/history?symbol_name=bitcoin&limit=50').then(result =>{
				this.utils.info('获取btn信息1...',result);
				let btn_data = result.data;
				let line_data = [];
				for(let i=0;i<btn_data.length;i++){
					line_data.push(btn_data[i][1]);
				}

				this.utils.info('line_data...',line_data);
				this.optionline.series[0].data = line_data;

				//初始化图表
				this.drawLine();
			}).catch(err =>{
				this.utils.info('获取btn信息1...err...再次请求',err);

				// this.getBtnFun();
			})
			
		},
		//获取用户信息 type 1 去请求btn信息 2不去
		getUserInfo(type){
            let user_id = 'dy' + this.user_info.user_id + this.utils.randomNumber(4);
			this.http.post(this.http._HOST + 'login/user_detail',{user_id:user_id}).then(result =>{
                this.utils.info('获取用户信息...',result);
                if(result.success){
                    let data = result.data;

                    this.user_info.dw_money = data.dw_money;
                    this.user_info.pay_password = data.pay_password;
					this.user_info.token = data.token;
					
					if(type == '1'){
						//获取进去btn信息
						this.getBtnInfo();
					}
                }
			})
		},
		//获取页面数据
		getBtnInfo(){
			let option = {
                token: this.user_info.token
            }
			this.http.post(this.http._HOST + 'wave/show_btc',option).then(result =>{
				this.utils.info('获取页面数据...',result);
				if(result.success){
					this.page_data = result.data;

					//先清除定时器
					if(this.timeInterval){
						clearInterval(this.timeInterval);	
					}
					if(this.timeResultInterval){
						clearInterval(this.timeResultInterval);	
					}
					if(this.page_data.user_postbtc){//下注了
						this.page_data.user_postbtc.game_time = parseInt(Math.abs(this.page_data.user_postbtc.game_time)) + 30;
						this.game_time_tmp = parseInt(Math.abs(this.page_data.user_postbtc.game_time)) + 30;

						//时间倒计时
						this.timeResultInterval = setInterval(() =>{
							this.page_data.user_postbtc.game_time --;
							this.utils.info('购买期数开奖时间...',this.page_data.user_postbtc.game_time);
							if(this.page_data.user_postbtc.game_time == 0){
								this.utils.info('开奖...',);

								//请求开奖结果
								this.getDrawResult();
								
								clearInterval(this.timeResultInterval);

								//再次请求，获取新期数信息
								this.getBtnInfo();
							}
						},1000)
					}else{//没下注
						this.page_data.next_time = parseInt(Math.abs(this.page_data.next_time)) + 30;
						this.next_time_tmp = parseInt(Math.abs(this.page_data.next_time)) + 30;
						//时间倒计时
						this.timeInterval = setInterval(() =>{
							this.page_data.next_time --;
							this.utils.info('当前期数剩余时间...',this.page_data.next_time);
							if(this.page_data.next_time == 0){
								this.utils.info('时间结束，获取新期数信息...',);

								clearInterval(this.timeInterval);

								//再次请求，获取新期数信息
								this.getBtnInfo();
							}
						},1000)
					}
					
				}
			})
		},
		//请求开奖结果
		getDrawResult(){
			let option = {
				btc_id: this.page_data.user_postbtc.btc_id,
				token: this.user_info.token
			}

			this.http.post(this.http._HOST + 'wave/new_result',option).then(result =>{
				this.utils.info('获取开奖结果...',result);
				if(result.success){
					
					
				}
			})

		}
		
	},
	beforeRouteEnter(to,from,next){
		console.log('to...',to);
		console.log('from...',from);
		next();
	}
}
</script>


